.bg-gradient_text {
  background: linear-gradient(180deg, #f8fdea 0%, #d9f28f 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.bg-gradient-bg {
  background: linear-gradient(180deg, #0b0d08 0%, #161a10 100%);
}
.bg-gradient-bg-iverse {
  background: linear-gradient(180deg, #161a10 0%, #0b0d08 100%);
}
.bg-button {
  background: linear-gradient(180deg, #dffea1 0%, #b2cb80 100%);
}
.bg-gradient_text .word {
  background: linear-gradient(180deg, #f8fdea 0%, #d9f28f 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
