.btn-cta {
  display: flex;
  padding: 10px 16px;
  justify-content: center;
  align-items: center;
  border-radius: 24px;
  text-align: center;
  will-change: transform;
  transform: translateZ(0);
  transition: transform 0.4s;
  background: #dffea1;
  color: #0b0d08;
  line-height: 100%;
}

.linkheader {
  transition: color 0.3s ease-in-out;
}

.linkheader:hover {
  color: #f8fdea;
}

.btn-cta.btn-principal {
  padding: 20px 24px;
  border-radius: 40px;
}

.btn-cta__ripple {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  transform: translateZ(0);
  border-radius: inherit;
  z-index: -1;
}

.btn-cta__ripple span {
  display: block;
  width: 100%;
  height: 100%;
  transform: translateY(101%);
  background: #f8fdea;
  border-radius: 50% 50% 0 0;
  transition: transform 0.5s cubic-bezier(0.4, 0, 0, 1),
    border-radius 0.5s cubic-bezier(0.4, 0, 0, 1);
}

/* Ripple effect on hover (for fine pointer devices) */
@media (pointer: fine) {
  .btn-cta:hover .btn-cta__ripple span {
    border-radius: 0;
    transform: translateY(0);
    transition-duration: 0.5s, 0.9s;
  }
}
