@import url(button.css);
@import url(gradients.css);
@import url(menu.css);
.split_mensagem_1 {
  font-kerning: none !important;
}
.gsap_container_sMensagem {
  display: grid;
  justify-content: center;
  align-items: center;
}
.gsap_container_sMensagem > * {
  grid-area: 1 / 1 / 2 / 2;
}
html.lenis,
html.lenis body {
  height: auto;
}

.lenis.lenis-smooth {
  scroll-behavior: auto !important;
}

.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}

.lenis.lenis-stopped {
  overflow: hidden;
}

.lenis.lenis-smooth iframe {
  pointer-events: none;
}
.cards-container {
  display: grid;
}
.cards-container > ul {
  grid-area: 1 / 1;
}
