.btn-cta {
  text-align: center;
  will-change: transform;
  color: #0b0d08;
  background: #dffea1;
  border-radius: 24px;
  justify-content: center;
  align-items: center;
  padding: 10px 16px;
  line-height: 100%;
  transition: transform .4s;
  display: flex;
  transform: translateZ(0);
}

.linkheader {
  transition: color .3s ease-in-out;
}

.linkheader:hover {
  color: #f8fdea;
}

.btn-cta.btn-principal {
  border-radius: 40px;
  padding: 20px 24px;
}

.btn-cta__ripple {
  border-radius: inherit;
  z-index: -1;
  display: block;
  position: absolute;
  inset: 0;
  overflow: hidden;
  transform: translateZ(0);
}

.btn-cta__ripple span {
  background: #f8fdea;
  border-radius: 50% 50% 0 0;
  width: 100%;
  height: 100%;
  transition: transform .5s cubic-bezier(.4, 0, 0, 1), border-radius .5s cubic-bezier(.4, 0, 0, 1);
  display: block;
  transform: translateY(101%);
}

@media (pointer: fine) {
  .btn-cta:hover .btn-cta__ripple span {
    border-radius: 0;
    transition-duration: .5s, .9s;
    transform: translateY(0);
  }
}

